import React from 'react';

const checkLengthStyle = num => {
  const color = num === 'Infinity' && '#ff0000';
  const fontSize = num.toString().length > 10 && '25px';
  return {
    color,
    fontSize
  };
};

export default ({
  currentNumber = '0',
  lastNumber = '',
  currentOperator = '',
  opOperator,
  history = []
}) => {
  let topNumber = lastNumber;
  if (currentOperator === '' && lastNumber === '0') {
    topNumber = ' ';
  }
  if (opOperator === 'sq') {
    topNumber = (
      <span>
        {lastNumber}
        <sup>2</sup>
      </span>
    );
  } else if (opOperator === 'root') {
    topNumber = <span>√{lastNumber}</span>;
  } else if (opOperator === 'sin') {
    topNumber = <span>{`sin(${lastNumber})`}</span>;
  }
  // else if (lastOperator === '')
  //   topNumber = <span style={{ color: '#1abc9c' }}>{lastNumber}</span>;
  return (
    <div className="row displayBox">
      <p
        className="displayEarlier"
        id="displayResult"
        style={checkLengthStyle(lastNumber, 2)}
      >
        {topNumber}
      </p>
      <p
        className="displayOperator"
        id="displayOperator"
        style={{
          color:
            (currentOperator === '' || currentOperator === 'equals') &&
            '#1abc9c'
        }}
      >
        {currentOperator || '+'}
      </p>
      <p
        className="displayResult"
        id="displayResult"
        style={checkLengthStyle(currentNumber, 1)}
      >
        {currentNumber}
      </p>
    </div>
  );
};
