import React from 'react';
import redo from '../img/redo.png';
import undo from '../img/undo.png';

const updateCurrent = (currentNumber, input) => {
  let nowNumber = currentNumber;
  const number = parseFloat(currentNumber, 10);
  switch (input) {
    case 'plus_minus':
      if (number > 0) nowNumber = `-${currentNumber}`;
      else if (number < 0) nowNumber = currentNumber.replace('-', '');
      break;
    case 'decimal':
      if (currentNumber.indexOf('.') === -1 && currentNumber.length < 8) {
        nowNumber = `${currentNumber}.`;
      }
      break;
    default:
      if (number === 0) nowNumber = `${input}`;
      else if (currentNumber.length < 9) nowNumber = `${currentNumber}${input}`;
  }
  return nowNumber;
};
export default ({
  currentNumber,
  lastNumber,
  currentOperator,
  opOperator,
  history,
  historyIndex,
  updateNumber,
  onUndoClick,
  onRedoClick
}) => {
  const onCClick = () => {
    updateNumber({
      currentNumber: '0',
      currentOperator: '',
      lastNumber: '0',
      opOperator: ''
    });
  };
  const onRootClick = () => {
    if (currentNumber === 'Infinity' || lastNumber === 'Infinity') return;
    const number = Math.sqrt(parseFloat(currentNumber, 10));
    if (currentOperator !== '') {
      updateNumber({
        lastNumber,
        opOperator: 'pn',
        currentNumber: `${number}`,
        currentOperator
      });
    } else
      updateNumber({
        lastNumber: `${currentNumber}`,
        opOperator: 'root',
        currentNumber: `${number}`,
        currentOperator: ''
      });
  };
  const onSqClick = () => {
    if (currentNumber === 'Infinity' || lastNumber === 'Infinity') return;
    const number =
      parseFloat(currentNumber, 10) * parseFloat(currentNumber, 10);
    if (currentOperator !== '') {
      updateNumber({
        lastNumber,
        opOperator: 'pn',
        currentNumber: `${number}`,
        currentOperator
      });
    } else
      updateNumber({
        opOperator: 'sq',
        lastNumber: `${currentNumber}`,
        currentNumber: `${number}`,
        currentOperator: ''
      });
  };
  const onSinClick = () => {
    if (currentNumber === 'Infinity' || lastNumber === 'Infinity') return;
    const number = Math.sin(parseFloat(currentNumber, 10));
    if (currentOperator !== '') {
      updateNumber({
        lastNumber,
        opOperator: 'pn',
        currentNumber: `${number}`,
        currentOperator
      });
    } else
      updateNumber({
        opOperator: 'sin',
        lastNumber: `${currentNumber}`,
        currentNumber: `${number}`,
        currentOperator: ''
      });
  };
  const onNumberClick = event => {
    if (currentNumber === 'Infinity' || lastNumber === 'Infinity') return;

    if (opOperator !== '') return;
    const input = event.target.id;
    const nowNumber = updateCurrent(currentNumber, input);
    updateNumber({
      lastNumber,
      currentOperator,
      opOperator: '',
      currentNumber: nowNumber,
      numberClicked: true
    });
  };
  const numClass = 'btn btn-calc hvr-radial-out';
  return (
    <div className="col-md-9">
      <div className="row">
        <button
          className="btn clear hvr-back-pulse"
          onClick={onCClick}
          id="clear"
        >
          C
        </button>
        <button
          onClick={onUndoClick}
          className="btn btn-calc hvr-radial-out"
          id="undo"
          disabled={historyIndex === history.length}
        >
          <img alt="#" src={undo} />
        </button>
        <button
          className="btn btn-calc hvr-radial-out hvr-radial-out"
          onClick={onRedoClick}
          disabled={historyIndex === 0}
          id="redo"
        >
          <img alt="#" src={redo} />
        </button>
      </div>
      <div className="row">
        <button
          className="btn btn-calc hvr-radial-out"
          onClick={onSinClick}
          id="sin"
        >
          sin
        </button>
        <button
          onClick={onRootClick}
          className="btn btn-calc hvr-radial-out"
          id="sqrt"
        >
          √
        </button>
        <button
          className="btn btn-calc hvr-radial-out hvr-radial-out"
          onClick={onSqClick}
          id="square"
        >
          x<sup>2</sup>
        </button>
      </div>
      <div className="row">
        <button onClick={onNumberClick} className={numClass} id={7}>
          7
        </button>
        <button onClick={onNumberClick} className={numClass} id={8}>
          8
        </button>
        <button onClick={onNumberClick} className={numClass} id={9}>
          9
        </button>
      </div>
      <div className="row">
        <button onClick={onNumberClick} className={numClass} id={4}>
          4
        </button>
        <button onClick={onNumberClick} className={numClass} id={5}>
          5
        </button>
        <button onClick={onNumberClick} className={numClass} id={6}>
          6
        </button>
      </div>
      <div className="row">
        <button onClick={onNumberClick} className={numClass} id={1}>
          1
        </button>
        <button onClick={onNumberClick} className={numClass} id={2}>
          2
        </button>
        <button onClick={onNumberClick} className={numClass} id={3}>
          3
        </button>
      </div>
      <div className="row">
        <button onClick={onNumberClick} className={numClass} id={'plus_minus'}>
          &#177;
        </button>
        <button onClick={onNumberClick} className={numClass} id={0}>
          0
        </button>
        <button onClick={onNumberClick} className={numClass} id="decimal">
          .
        </button>
      </div>
    </div>
  );
};
