import React from 'react';

const updateNumber = (
  lastNumber,
  currentOperator,
  currentNumber,
  numberClicked
) => {
  lastNumber = parseFloat(lastNumber, 10);
  currentNumber = parseFloat(currentNumber, 10);
  switch (currentOperator) {
    case '+':
      return lastNumber + currentNumber;
    case '-':
      return lastNumber - currentNumber;
    case 'x':
      if (currentNumber === 0 && !numberClicked) return lastNumber;
      return lastNumber * currentNumber;
    case '/':
      if (currentNumber === 0 && !numberClicked) return lastNumber;
      if (isNaN(currentNumber)) return 'Infinity';
      return lastNumber / currentNumber;
    default:
      return currentNumber === 0 ? lastNumber : currentNumber;
  }
};
const getCurrentOperator = event => {
  const input = event.target.id;
  switch (input) {
    case 'add':
      return '+';
    case 'subtract':
      return '-';
    case 'multiply':
      return 'x';
    case 'divide':
      return '/';
    default:
      return '';
  }
};
export default ({
  currentNumber,
  lastNumber,
  currentOperator,
  opOperator,
  updateOperator,
  numberClicked
}) => {
  const operatorClass = 'btn btn-operation hvr-fade';
  const onOpClick = event => {
    if (currentNumber === 'Infinity' || lastNumber === 'Infinity') return;

    lastNumber = `${updateNumber(
      lastNumber,
      currentOperator,
      currentNumber,
      numberClicked
    )}`;
    currentOperator = getCurrentOperator(event);
    currentNumber = '0';

    updateOperator({
      currentNumber,
      lastNumber,
      currentOperator,
      opOperator: ''
    });
  };
  return (
    <div className="col-md-3 operationSide">
      <button id="divide" onClick={onOpClick} className={operatorClass}>
        ÷
      </button>
      <button id="multiply" onClick={onOpClick} className={operatorClass}>
        ×
      </button>
      <button id="subtract" onClick={onOpClick} className={operatorClass}>
        −
      </button>
      <button id="add" onClick={onOpClick} className={operatorClass}>
        +
      </button>
      <button
        id="equals"
        onClick={onOpClick}
        className="btn btn-operation equals hvr-back-pulse"
      >
        =
      </button>
    </div>
  );
};
