import React, { Component } from 'react';
import DisplayBox from './components/displaybox';
import NumberBox from './components/numberbox';
import OperatorBox from './components/operatorbox';

import './css/app.css';

const localId = 'reduxCalculator';
let loHistory = [];
if (localStorage.getItem(localId)) {
  loHistory = JSON.parse(localStorage.getItem(localId));
}

class App extends Component {
  state = {
    currentNumber: '0',
    lastNumber: '0',
    currentOperator: '',
    opOperator: '',
    history: loHistory,
    historyIndex: 0
  };
  updateHistory = newHis => {
    const { history } = this.state;
    if (
      history.length === 0 ||
      JSON.stringify(history[history.length - 1]) !== JSON.stringify(newHis)
    ) {
      history.push(newHis);
      // localStorage.setItem(localId, JSON.stringify(history));
      this.setState({ history, historyIndex: 0 });
    }
  };
  updateNumber = ({
    currentNumber,
    lastNumber,
    currentOperator,
    opOperator,
    numberClicked
  }) => {
    this.setState({
      currentNumber,
      lastNumber,
      currentOperator,
      opOperator,
      numberClicked
    });
    this.updateHistory({
      currentNumber,
      lastNumber,
      currentOperator,
      opOperator,
      historyIndex: 0
    });
  };
  updateOperator = ({
    currentNumber,
    lastNumber,
    currentOperator,
    opOperator
  }) => {
    this.setState({
      currentNumber,
      lastNumber,
      currentOperator,
      opOperator,
      historyIndex: 0,
      numberClicked: false
    });
    this.updateHistory({
      currentNumber,
      lastNumber,
      currentOperator,
      opOperator
    });
  };
  onUndoClick = () => {
    const { history, historyIndex } = this.state;
    const curr = history[history.length - 1 - historyIndex];
    this.setState({
      currentNumber: curr.currentNumber,
      lastNumber: curr.lastNumber,
      currentOperator: curr.currentOperator,
      lastOperator: curr.lastOperator,
      opOperator: curr.opOperator,
      historyIndex: historyIndex + 1
    });
  };
  onRedoClick = () => {
    const { history, historyIndex } = this.state;
    const curr = history[history.length - historyIndex];
    this.setState({
      currentNumber: curr.currentNumber,
      lastNumber: curr.lastNumber,
      currentOperator: curr.currentOperator,
      lastOperator: curr.lastOperator,
      opOperator: curr.opOperator,
      historyIndex: historyIndex - 1
    });
  };
  render() {
    return (
      <div className="container">
        <div className="col-md-4 col-md-offset-4 calculator" align="center">
          <DisplayBox {...this.state} />
          <div className="row numberPad">
            <NumberBox
              {...this.state}
              updateNumber={this.updateNumber}
              updateOperator={this.updateOperator}
              onUndoClick={this.onUndoClick}
              onRedoClick={this.onRedoClick}
            />
            <OperatorBox {...this.state} updateOperator={this.updateOperator} />
          </div>
        </div>
      </div>
    );
  }
}

export default App;
